// This scss file is associatied witht eh test.js component. All styles in this section should be relative to this component only.

// Imports the global colors
@import './_colors.scss';


// Imports the global fonts
@import url('https://fonts.googleapis.com/css?family=Dosis:800|Montserrat:400,900|Overpass:400,900|Rubik+Mono+One|Rubik:400,900');

// font family variables
// $rubik: 'Rubik', sans-serif;
// $rubik-mono: 'Rubik Mono One', sans-serif;
// $montserrat: 'Montserrat', sans-serif;
// $overpass: 'Overpass', sans-serif;
// $dosis: 'Dosis', sans-serif;
$righteous: 'Righteous', cursive;
$arial: Arial, Helvetica, sans-serif;
// Function that maps throught the global color object to make the color designation simpler below.
@function color($color-name) {
    @return map-get($colors, $color-name)
};

// mixin 
@mixin breakpoint-desktop {
    @media (max-width: 800px) { @content };
      
  }
@mixin breakpoint-phone {
    @media (max-width: 415px) { @content };
      
  }


// Component styles start here. Note, the body element is only there as an example of how to nest using scss

div.follow-parent-box {
    position: fixed;
    left: 0;
    bottom: 0;
    // width: 220px;
    // height: 100%;
    z-index: 1000;
    background: yellow;
    @include breakpoint-phone {
        width: 10vw;
    }
        div.follow-link-container {
            position: absolute;
            bottom: 5vh;
            width: 100px;
            background: transparent;
            z-index: 1000;
       
                    a.follow-BTN {
                        position: absolute;
                        margin:  0px;
                        padding: 0px;
                        border: none;
                        background: transparent;
                        font-size: 20px;
                        font-weight: bolder;
                        font-family: $arial;
                        letter-spacing:  4px;
                        left: 30px;
                        bottom: -3vh;
                        color: #303030;
                        z-index: 980;
                        cursor: pointer;
                        &:hover {
                                color: #787878;
                            }
                        }
                    a#instagram {
                        letter-spacing: 3px;
                    }
                    a#twitter {
                        letter-spacing: 5.5px;
                    }
                    a.follow-link {
                        position: relative;
                        margin:  15px 0px;
                        padding: 0px;
                        border: none;
                        color: #303030;
                        font-family: $arial;   
                        font-weight: 100;
                        font-size: 16px;
                        letter-spacing: 4px;
                        left: 40px;
                        text-decoration: none;
                        background: transparent;
                        display: block;
                        text-align: left;
                        &:hover {
                            font-style: italic;
                            color: #787878;
                    }
                }
            
        }
       
}